//
function dotArr() {
    const dataArr: Array<EChartsOption> = [];
    for (let i = 0; i < 100; i++) {
        if (i % 2 === 0) {
            dataArr.push({
                name: (i + 1).toString(),
                value: 1,
                itemStyle: {
                    color: "#005dff",//
                    borderWidth: 1, //
                    borderColor: "#314076",//
                },
            });
        } else {
            dataArr.push({
                name: (i + 1).toString(),
                value: 2,
                itemStyle: {
                    color: "rgba(0,0,0,0)",
                    borderWidth: 0,
                    borderColor: "rgba(0,0,0,0)",
                },
            });
        }
    }
    return dataArr;
}
