import request from '../axios';
interface IParams{
  type:string
}
 //
 export const lineInfo =  (params:IParams) => {
    return request({
        url: '/emptech-data-screen/operationScreen/lineInfo',
        method: 'get',
        params: {
            ...params,
        }
    })
}
